.social-block {
  position: relative;

  .social-block__list {
    & li {
      opacity: 0;
      transition-property: opacity;
      transition-duration: 0.3s;
      transition-timing-function: ease;
    }
  }

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;

      & li {
        opacity: 1;
      }
      & li:nth-child(1) {
        transition-delay: 0s;
      }
      & li:nth-child(2) {
        transition-delay: 0.1s;
      }
      & li:nth-child(3) {
        transition-delay: 0.2s;
      }
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;

  body.default & {
    color: $purple-dark;
  }
  body.purple &{
    color: $purple-light;
  }
  body.blue &{
    color: $blue;
  }
  body.light-blue &{
    color: $blue-light;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    @include center;
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $c-light;
    border: 2px solid $c-light;
    z-index: -1;
    transform: translate(-50%, -50%) scale(0.6);
    transition: transform 0.5s cubic-bezier(.33,.65,.82,1.49);
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
  }

  &:hover,
  .social-block--active & {

    body.default & {
      color: $purple-light;
    }
    body.purple &{
      color: $purple-dark;
    }
    body.blue &{
      color: $blue-light;
    }
    body.light-blue &{
      color: $blue;
    }


    &::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    body.default & {
      color: $purple-light;
    }
    body.purple &{
      color: $purple-dark;
    }
    body.blue &{
      color: $blue-light;
    }
    body.light-blue &{
      color: $blue;
    }
    outline: none;

    &::before {
      body.default & {
        border-color: $purple-light;
      }
      body.purple &{
        border-color: $purple-dark;
      }
      body.blue &{
        border-color: $blue-light;
      }
      body.light-blue &{
        border-color: $blue;
      }
    }

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &:active,
  &:active:focus {
    body.default &,
    body.purple &,
    body.blue &,
    body.light-blue & {
      color: $c-light;
    }

    &::before {
      width: 100%;
      height: 100%;
      body.default & {
        background-color: $purple-light;
        border-color: $purple-light;
      }
      body.purple &{
        background-color: $purple-dark;
        border-color: $purple-dark;
      }
      body.blue &{
        background-color: $blue-light;
        border-color: $blue-light;
      }
      body.light-blue &{
        background-color: $blue;
        border-color: $blue;
      }
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: $c-dark;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: $c-dark;
  }

  &:hover {
    body.default & {
      color: $purple-light;
    }
    body.purple &{
      color: $purple-dark;
    }
    body.blue &{
      color: $blue-light;
    }
    body.light-blue &{
      color: $blue;
    }
  }

  &:focus {
    color: $c-purple;
    background-color: $c-light;
    outline: none;
    body.default & {
      border-color: $purple-light;
    }
    body.purple &{
      border-color: $purple-dark;
    }
    body.blue &{
      border-color: $blue-light;
    }
    body.light-blue &{
      border-color: $blue;
    }
  }

  &:active,
  &:active:focus {
    body.default &,
    body.purple &,
    body.blue &,
    body.light-blue & {
      border-color: $c-light;
    }
    background-color: $c-light;
  }

  svg {
    @include center;
  }

  &--fb {
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}
