@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeInRulesNumberBackground {
  0% {
    transform: scale3d(0, 0, 1);
    opacity: 0;
  }

  100% {
    transform: scale3d(1, 1, 1);
    opacity: 1;
  }
}

@keyframes fadeInRulesNumber {
  0%,
  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeInRulesName {
  0% {
    transform: translateX(40px);
    opacity: 0;
  }

  90% {
    opacity: 0.5;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fadeInGoBtn {
  0% {
    max-width: 0;
  }
  100% {
    max-width: 200px;
  }
}

@keyframes fly-flamingo {
  0% {
    transform: rotate(0) translateY(0);
  }

  50% {
    transform: rotate(-10deg) translateY(5%);
  }

  100% {
    transform: rotate(0) translateY(0);
  }
}

@keyframes fly-leaf-right {
  0% {
    transform: rotate(0);
  }

  15% {
    transform: rotate(10deg);
  }

  30% {
    transform: rotate(-7deg);
  }

  45% {
    transform: rotate(7deg);
  }

  65% {
    transform: rotate(-3deg);
  }

  85% {
    transform: rotate(3deg);
  }

  100% {
    transform: rotate(0);
  }
}

@keyframes fly-leaf-left {
  0% {
    transform: rotate(0);
  }

  15% {
    transform: rotate(-10deg);
  }

  30% {
    transform: rotate(7deg);
  }

  45% {
    transform: rotate(-7deg);
  }

  65% {
    transform: rotate(3deg);
  }

  85% {
    transform: rotate(-3deg);
  }

  100% {
    opacity: 1;
    transform: rotate(0);
  }
}

@keyframes rotate-to-right {
  0% {
    transform:
      translate(-4rem, 0) rotate(0deg);
  }
  25% {
    transform:
      translate(-3rem, 0) rotate(90deg);
  }
  50% {
    transform:
      translate(-2rem, 0) rotate(180deg);

  }
  75% {
    transform:
      translate(-1rem, 0) rotate(270deg);
  }
  100% {
    transform:
      translate(0, 0) rotate(360deg);
  }
}

@keyframes rotate-to-left {
  0% {
    transform:
      translate(4rem, 0) rotate(0deg);
  }
  25% {
    transform:
      translate(3rem, 0) rotate(-90deg);
  }
  50% {
    transform:
      translate(2rem, 0) rotate(-180deg);

  }
  75% {
    transform:
      translate(1rem, 0) rotate(-270deg);
  }
  100% {
    transform:
      translate(0, 0) rotate(-360deg);
  }
}
